@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Roboto:300,300i,400,400i,500,500i,700");
html,
body {
  margin: 0;
  padding: 0;
}

span {
  color: #fc9ece;
}

.wrapper {
  background: #fff;
  box-shadow: 3px 3px 15px rgba(171, 171, 171, 0.2);
  border-radius: 15px;
  padding: 1rem;
  margin: 30px 0;
}

img {
  width: 100%;
  border-radius: 15px;
}

img.profile {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}

img.wide {
  width: 100%;
  height: auto;
  margin: 50px 0;
  border-radius: 0;
}

img:nth-child(2) {
  margin: 10px 0;
}

body {
  color: #ababab;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Segoe UI", sans-serif;
  font-size: 1.125rem;
  line-height: 3rem;
  margin: 1.5rem 0;
}

h1 {
  font-size: 45px;
  font-weight: 500;
  line-height: 49px;
  color: #fc9ece;
}

h2 {
  color: #545454;
  font-size: 1.7578125rem;
  line-height: 3rem;
}

h3 {
  font-size: 20px;
}

h4 {
  font-size: 13px;
}

h5 {
  font-size: 1.25rem;
  line-height: 3rem;
}

a {
  text-decoration: none;
  color: #ababab;
}

.clearfix::after {
  clear: both;
  content: '';
  display: table;
}

.center {
  text-align: center;
}

.padding {
  padding: 50px 0;
}

.margin {
  margin: 50px 0;
}

.text--primary {
  color: #fc9ece;
}

.text--pink {
  color: #fbcacb;
}

.text--bold {
  font-weight: bold;
}

.text--uppercase {
  text-transform: uppercase;
}

.header {
  width: 300px;
  margin-top: 20px;
}

